	module.exports = {"data":{"post":{"html":"<p>As further proof that Hamburg is not only the city of eternal rain, but also the center of the known blogging universe, this Sunday we have not one, but <em>two</em> independently organized face-to-face blogging events: <a href=\"http://www.hamburgstories.org/wiki/pmwiki.php/Main/BloggerBrunch\">BloggerBrunch</a><br />\nin the morning in the city and <a href=\"http://wiki.langbartels.de/wiki/index.cfm?BlogGrill\">Blog Grill 3</a> in the afternoon closer to Lüneburg. Alas, we have visitors from abroad on Sunday, and I won't be able to make it to either event.</p>","fields":{"title":"Center of the Universe","date":"09 Jul 2004","url":"https://papascott.de/2004-07-09-center-of-the-universe/"}}},"pathContext":{"slug":"/2004-07-09-center-of-the-universe/","prev":{"fields":{"title":"99 Luftballons, Side by Side Comparison","date":"09 Jul 2004","slug":"/2004-07-09-99-luftballons-side-by-side-comparison/"}},"next":{"fields":{"title":"No, not Kaiser Franz","date":"09 Jul 2004","slug":"/2004-07-09-no-not-kaiser-franz/"}}}}

